import { Actions } from '../actions'
import { home } from '../../config'

const homeInit = {
  stateHistory: [],
  // currentState: home.AppStateTypes.SERIES,
  currentState: home.AppStateTypes.MAP
  // activeSeries: 'fda17bae-0335-4f0d-9065-dc8d7aa23ed1'
}

export default (state = homeInit, action) => {
  const { type, data } = action
  const { stateHistory, currentState } = state

  switch (type) {
    case Actions.HOME_STATE_BACK:
      const newStateHistory = [...stateHistory]
      const newCurrentState = newStateHistory > 0 ? newStateHistory.pop() : home.AppStateTypes.MAP
      return {
        ...state,
        stateHistory: newStateHistory,
        currentState: newCurrentState
      }

    case Actions.HOME_CHANGE_STATE:
      const newHistory = [...stateHistory, currentState]
      return {
        ...state,
        history: newHistory,
        currentState: data
      }

    case Actions.HOME_CHANGE_ACTIVE_SERIES:
      return {
        ...state,
        activeSeries: data
      }

    default:
      return state
  }
}
