import React from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import IconButton from '@material-ui/core/IconButton'
import CancelIcon from '@material-ui/icons/Cancel'
import { endpoints } from '../../../config'
import ZoomInIcon from '@material-ui/icons/ZoomIn'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
// import MenuIcon from '@material-ui/core/Menu'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {

  },
  img: {
    maxWidth: '100%'
  },
  imgContainer: {
    width: 100
  }
})

const SeriesSelected = ({ classes, series, loading, onClose, onClick, onZoomTo }) => {
  if (loading || !series) return 'LOADING...'
  let thumb = null
  try {
    thumb = `${endpoints.cdn}/${series.featured.paths.thumb}`
  }
  catch (e) {}
  return (
    <List disablePadding dense>
      <ListItem className={classes.root} button>
        <div className={classes.imgContainer} onClick={onClick}>
          <img alt='thumbnail' src={thumb} className={classes.img} />
        </div>
        <ListItemText
          onClick={onClick}
          primary={series.name || loading}
          secondary={`${series.photoCount || 0} Photos`}
        />
        <ListItemSecondaryAction>
          <IconButton aria-label='Close' onClick={onZoomTo}>
            <ZoomInIcon />
          </IconButton>
          <IconButton aria-label='Close' onClick={onClose}>
            <CancelIcon />
          </IconButton>
        </ListItemSecondaryAction>
      </ListItem>
    </List>
  )
}

SeriesSelected.propTypes = {
  classes: PropTypes.object.isRequired,
  series: PropTypes.object,
  loading: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
  onZoomTo: PropTypes.func.isRequired
}

export default withStyles(style)(SeriesSelected)
