import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import Avatar from '@material-ui/core/Avatar'

import { withStyles } from '@material-ui/core/styles'
import numeral from 'numeral'

const style = theme => ({
  avatar: {
    background: theme.palette.primary.dark,
    border: `2px solid ${theme.palette.primary.contrastText}`,
    color: theme.palette.primary.contrastText
  },
  avatarActive: {
    background: theme.palette.secondary.main,
    border: `2px solid ${theme.palette.secondary.contrastText}`,
    color: theme.palette.secondary.contrastText,
    zIndex: 1000
  }
})

const Cluster = ({ classes, seriesCount, active, onClick }) => {
  return (
    <Avatar onClick={onClick} className={classNames(classes.avatar, active ? classes.avatarActive : null)}>
      {numeral(seriesCount).format('0,0')}
    </Avatar>
  )
}

Cluster.propTypes = {
  classes: PropTypes.object.isRequired,
  seriesCount: PropTypes.number,
  onClick: PropTypes.func.isRequired,
  active: PropTypes.bool
}

export default withStyles(style)(Cluster)
