import React, { useMemo } from 'react'
import PropTypes from 'prop-types'
import Home from './views/Home'
import CssBaseline from '@material-ui/core/CssBaseline'

import UploaderConnect from './graphql/Uploader.connect'
import SeriesConnect from './graphql/Series.connect'
import HomeMapConnect from './graphql/HomeMap.connect'

import { connect } from 'react-redux'
import { ApolloProvider } from 'react-apollo'
import { createApolloClient } from './lib/useGraphQL'
import { IntlWrapper } from './translations'

import actions from './redux/actions'
import useAuthentication from './lib/useAuthentication'

import { AppContext } from './contexts'
import { home as homeConfig } from './config'
// Here's our global overrides css
import './App.css'

/**
 * This is the over-arching wrapper for the app. It combines all the
 * necessary pieces like authentication, context and Apollo
 *
 * Use this to set app-wide context. Hopefully we can do this and leave redux out of the loop for now.
 *
 * Also for now I'm trying to put all the HOC and top-level wrapping in the
 * same place. Time will tell if that's a good strategy.
 * @param {*} param0
 */

const App = ({ currentState, setHomeState }) => {
  const { signedIn, authData, signOut, authScreen, accessToken } = useAuthentication()

  // This is the root node that basically says "Do a React here"
  const apolloClient = useMemo(() => createApolloClient(accessToken), [accessToken])

  if (!signedIn) return authScreen

  // These are the things the rest of the app needs to find:
  const appContext = {
    signOut,
    signedIn,
    currentUser: authData
  }

  return (
    <ApolloProvider client={apolloClient}>
      <IntlWrapper>
        <AppContext.Provider value={appContext}>
          <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
          <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
            <div style={{ flexGrow: 1, overflow: 'hidden' }}>
              {[
                homeConfig.AppStateTypes.MAP,
                homeConfig.AppStateTypes.LIST,
                homeConfig.AppStateTypes.USER
              ].indexOf(currentState) > -1 && (
                <Home currentState={currentState} setHomeState={setHomeState}>
                  {currentState === homeConfig.AppStateTypes.MAP && (
                    <HomeMapConnect />
                  )}
                  {currentState === homeConfig.AppStateTypes.LIST && (
                    <div>LIST</div>
                  )}
                  {currentState === homeConfig.AppStateTypes.USER && (
                    <div>USER</div>
                  )}
                </Home>
              )}
              {[
                homeConfig.AppStateTypes.SERIES,
                homeConfig.AppStateTypes.NEW_SERIES_CAMERA,
                homeConfig.AppStateTypes.NEW_SERIES_LIBRARY
              ].indexOf(currentState) > -1 && (
                <SeriesConnect authData={authData} />
              )}
            </div>
            {/* Uploader is persistent across all views */}
            <div style={{ zIndex: 2000, flexGrow: 0 }}>
              <UploaderConnect />
            </div>
          </div>

        </AppContext.Provider>
      </IntlWrapper>
    </ApolloProvider>

  )
}

App.propTypes = {
  // From Redux
  currentState: PropTypes.string.isRequired,
  setHomeState: PropTypes.func.isRequired
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { currentState } = state.home
  return { currentState }
}

const mapDispatchToProps = dispatch => ({
  setHomeState: (newState) => {
    dispatch(actions.setHomeState(newState))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)
