import React, { useState, useRef, useMemo, useEffect } from 'react'
import ReactMapGL, { NavigationControl, Marker, FlyToInterpolator } from 'react-map-gl'
import PropTypes from 'prop-types'
import { easeCubic } from 'd3-ease'
import WebMercatorViewport, { getDistanceScales } from 'viewport-mercator-project'
import useComponentSize from '../../lib/useComponentSize'
import MyLocation from './markers/MyLocation'
import { useGeolocation } from '../../lib/sensors'
import StyleButtons from './controls/StyleButtons'
import MyLocationButton from './controls/MyLocationButton'
import { mapConfig } from '../../config'
import classNames from 'classnames'
import 'mapbox-gl/dist/mapbox-gl.css'
import './BaseMap.css'

/**
 * Note that this component technically has state but it is designed to be functionally stateless
 */
const BaseMap = (props) => {
  // pull what we need out of the props
  const {
    showMe,
    children,
    mapStyles,
    controlPadding,
    viewport: initViewport,
    bounds,
    lockToBounds,
    lockPan,
    initStyle,
    onViewportChange: incomingOnViewportChange,
    onTransitionStart: incomingOnTransitionStart,
    onTransitionEnd: incomingOnTransitionEnd,
    transitions,
    onMapDataEvent,
    stylechooser,
    ...passthroughProps
  } = props

  const mapContainerRef = useRef(null)
  const mapBoxRef = useRef(null)
  const { size } = useComponentSize(mapContainerRef, { width: 100, height: 50 })

  const [currStyle, setCurrStyle] = useState(initStyle)
  const [isTransitioning, setIsTransitioning] = useState(false)

  // This is the truth of the viewport!!
  const [viewport, setViewport] = useState({ ...defaultViewVals, ...initViewport })
  const [innerTransitions, setInnerTransitions] = useState(null)
  const [projection, setProjection] = useState(null)

  let myPosition = {}
  if (showMe) {
    myPosition = useGeolocation()
  }
  const geoMarker = (myPosition.latitude && myPosition.longitude)
    ? (
      <Marker key='myPos' className='myPosMarker' latitude={myPosition.latitude} longitude={myPosition.longitude} style={{ zIndex: 1 }}>
        <MyLocation position={myPosition} projection={projection} />
      </Marker>
    ) : null

  // Recalculate the projection if we need to but only when zoom changes
  useMemo(() => {
    if (!size || size.width < 10 || size.height < 10) return
    if (mapStyles.length <= currStyle) setCurrStyle(initStyle)
    try {
      const projection = getDistanceScales(viewport)
      // console.log('recalcProjection', { projection, size })
      setProjection(projection)
    }
    catch (e) {
      console.log('ERROR', viewport)
      console.error(e)
    }
  }, [viewport.zoom, size])

  const flyToMe = () => {
    if (!myPosition.latitude || !myPosition.longitude) return

    setInnerTransitions({
      transitionDuration: 2000,
      transitionInterpolator: new FlyToInterpolator(),
      transitionEasing: easeCubic
    })
    setViewport({
      ...viewport,
      latitude: myPosition.latitude,
      longitude: myPosition.longitude
    })
  }

  /**
   * Callback fired when the user changes the viewport object
   * @param {*} viewport
   */
  const onViewportChange = (viewportChange) => {
    const newViewport = { ...viewportChange }
    // Here we set/reset the bounds
    if (bounds && lockToBounds) {
      if (newViewport.longitude < bounds[0]) newViewport.longitude = bounds[0]
      else if (newViewport.longitude > bounds[2]) newViewport.longitude = bounds[2]

      if (newViewport.latitude < bounds[1]) newViewport.latitude = bounds[1]
      else if (newViewport.latitude > bounds[3]) newViewport.latitude = bounds[3]
    }
    if (lockPan) {
      newViewport.latitude = viewport.latitude
      newViewport.longitude = viewport.longitude
    }

    // console.log('onViewportChange', newViewport)
    // Fire the external callback so parent components can track viewport if they need to
    if (!isTransitioning && typeof incomingOnViewportChange === 'function') {
      incomingOnViewportChange({
        viewport: newViewport,
        size,
        projection,
        map: mapBoxRef.current && mapBoxRef.current.getMap()
      })
    }
    setViewport(newViewport)
  }

  const recalcZoomBounds = () => {
    // console.log('Should I recalc viewbounds?')
    if (!bounds || !size || size.width < 10 || size.height < 10 || isTransitioning) return
    // On the first run we're going to try to center the bounds.
    const [minlng, minlat, maxlng, maxlat] = bounds
    // console.log('yes, yesI should')
    try {
      const { longitude, latitude, zoom } = new WebMercatorViewport(viewport).fitBounds(
        [ [minlng, minlat], [maxlng, maxlat] ],
        { padding: 40, offset: [0, 0] }
      )
      console.log('Changing Zoom ', bounds, viewport.zoom, zoom, viewport)

      // console.log('recalcZoomBounds')
      setViewport({ ...viewport, longitude, latitude, zoom })
    }
    catch (e) {
      // Sometimes we don't have a valid bound. Don't make a big deal out of it.
      console.error(e)
    }
  }

  const handleTransitionStart = () => {
    setIsTransitioning(true)
    // console.log('handleTransitionStart (internal)')
    if (incomingOnTransitionStart) incomingOnTransitionStart()
  }
  const handleTransitionEnd = () => {
    setIsTransitioning(false)
    setInnerTransitions(null)
    // console.log('handleTransitionEnd (internal)')
    if (incomingOnTransitionEnd) incomingOnTransitionEnd()
    if (typeof incomingOnViewportChange === 'function') {
      incomingOnViewportChange({
        viewport,
        size,
        projection,
        map: mapBoxRef.current && mapBoxRef.current.getMap()
      })
    }
  }

  // RecalcZoombounds should only be done sparingly
  useMemo(recalcZoomBounds, [size, bounds])

  useEffect(() => {
    // console.log('SOMETHING CHANGED')
    setViewport({ ...viewport, ...initViewport })
  }
  , [initViewport])

  return (
    <div className={classNames('BaseMap', props.className)} ref={mapContainerRef} style={{ height: '100%', width: '100%' }}>
      <ReactMapGL
        {...viewport}
        {...(innerTransitions || transitions)}
        ref={map => {
          mapBoxRef.current = map
        }}
        height={size.height}
        width={size.width}
        mapStyle={currStyle < mapStyles.length ? mapStyles[currStyle].style : mapStyles[0].style}
        onData={onMapDataEvent}
        onTransitionStart={handleTransitionStart}
        onTransitionEnd={handleTransitionEnd}
        dragPan={!lockPan}
        onViewportChange={onViewportChange}
        mapboxApiAccessToken={mapConfig.mapBoxToken}
        {...passthroughProps}
      >
        {
          children && Array.isArray(children)
            ? children.map((c, idx) => c ? React.cloneElement(
              handleChild(c, viewport, size.height, size.width, projection),
              { key: `mapChild-${idx}` }
            ) : null)
            : handleChild(children, viewport, size.height, size.width, projection)
        }
        {/* Make sure the controls are above everything else */}
        { stylechooser && (
          <StyleButtons
            controlPadding={controlPadding}
            currStyle={currStyle}
            mapStyles={mapStyles}
            setCurrStyle={setCurrStyle}
          />
        )}
        {/* Augment children with some props they need to survive in this harsh world */}
        { !props.hideNavControls && (
          <NavigationControl
            onViewportChange={viewport => onViewportChange({ ...viewport, ...size })}
            showZoom={!props.touchZoom === false && !props.scrollZoom === false}
            showCompass={!props.touchRotate === false && !props.dragRotate === false}
          />
        )}
        {showMe && (
          <React.Fragment>
            {geoMarker}
            <MyLocationButton disabled={!myPosition || !myPosition.latitude} controlPadding={controlPadding} onClick={flyToMe} />
          </React.Fragment>
        )}
      </ReactMapGL>
    </div>
  )
}

BaseMap.propTypes = {
  // Anything else we want to add to the map
  children: PropTypes.any,
  deckelements: PropTypes.array,
  data: PropTypes.array,
  // Maybe we want map controls to be padded
  controlPadding: PropTypes.object,

  showMe: PropTypes.bool,

  // Separate transitions from viewport so we can set them independently
  transitions: PropTypes.object,
  initStyle: PropTypes.number,

  // Some switches to make things easier
  stylechooser: PropTypes.bool,
  lockPan: PropTypes.bool,
  lockRotation: PropTypes.bool,
  lockZoom: PropTypes.bool,
  lockToBounds: PropTypes.bool,
  hideNavControls: PropTypes.bool,

  // bounds of the map. may be locked or not [minlng, minlat], [maxlng, maxlat]
  bounds: PropTypes.array,

  // List of valid styles that will get picker buttons
  mapStyles: PropTypes.array,

  onMapDataEvent: PropTypes.func,
  onViewportChange: PropTypes.func

  // We get these props from calculating the window size at a higher level
  // size: PropTypes.shape({
  //   width: PropTypes.number.isRequired,
  //   height: PropTypes.number.isRequired
  // })
}

BaseMap.defaultProps = {
  controlPadding: {},
  stylechooser: true,
  // viewport: {
  //   zoom: 12,
  //   latitude: 49.3050394,
  //   longitude: -123.1398254
  // },
  initStyle: 0,
  mapStyles: mapConfig.styles,
  lockToBounds: false
}

export default BaseMap

const defaultViewVals = {
  latitude: 49.3050394,
  longitude: -123.1398254,
  bearing: 0,
  pitch: 0,
  zoom: 12
}

function handleChild (child, viewport, height, width, projection) {
  return typeof child === 'function'
    ? child(viewport, height, width, projection)
    : child
}
