import gql from 'graphql-tag'

export const allSeries = gql`
  query allSeries($limit: Int, $nextToken: String) {
    allSeries(limit: $limit, nextToken: $nextToken) {
      seriesSummaries {
        id
        name
        photoCount
        latitude
        longitude
        direction
        featured {
          paths {
            thumb
          }
        }
      }
      nextToken
    }
  }
`

export const pollQuery = gql`
  query pollQuery($lastCheck: String!, $limit: Int, $nextToken: String) {
    changedSeries(lastCheck: $lastCheck, limit: $limit, nextToken: $nextToken){
      seriesSummaries {
        id
        name
        photoCount
        latitude
        longitude
        direction
        seriesStatus
        featured {
          paths {
            thumb
          }
        }
      }
    }
  }
`

export const userSeries = gql`
  query userSeries($createdBy: String!, $limit: Int, $nextToken: String) {
    userSeries(createdBy: $createdBy, limit: $limit, nextToken: $nextToken) {
      id
      name
      photoCount
      latitude
      longitude
      direction
      createdBy
    }
  }
`

export const seriesFromList = gql`
  query seriesFromList($seriesList: [ID]) {
    arraySeries(seriesIds: $seriesList) {
      id
      name
      photoCount
      latitude
      longitude
      direction
      createdBy
    }
  }
`

export const seriesWithTag = gql`
  query seriesWithTag($tag: String!, $limit: Int) {
    tagSeries(tag: $tag, limit: $limit) {
      arraySeries {
        id
        name
        photoCount
        latitude
        longitude
        direction
        createdBy
      }
      nextToken
    }
  }
`

export const tagSearch = gql`
  query tagSearch($returnNum: Int, $tag: String) {
    tags(returnNum: $returnNum, tag: $tag ){
      name
      count
    }
  }
`

export const getWholeSeries = gql`
  query getWholeSeries($id: ID!) {
    series(id: $id) {
      id
      name
      note
      meta
      # createdOn
      # updatedOn
      latitude
      longitude
      direction
      photoCount
      featured {
        id
        takenOn
        paths {
          thumb
        }
      }
      # latest {
      #   id
      #   paths {
      #     thumb
      #   }
      #   createdBy
      #   createdOn
      #   takenOn
      #   updatedOn
      # }
      photos {
        id
        meta
        createdBy
        # createdOn
        takenOn
        # updatedOn
        tags {
          label
          typeId
          coords {
            x
            y
          }
        }
        paths {
          large
          orig
          thumb
          medium
        }
      }
    }
  }
`
