import { createStore, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk'
import rootReducer from './reducers'

// import api from '../middleware/api'
// https://github.com/reduxjs/redux/blob/master/examples/real-world/src/middleware/api.js

/**
 * On Dev we want our devTools available
 */
const configureStoreDev = preloadedState => {
  const { devToolsEnhancer } = require('redux-devtools-extension')
  const { createLogger } = require('redux-logger')
  const store = createStore(
    rootReducer,
    preloadedState,
    compose(
      devToolsEnhancer(),
      applyMiddleware(
        thunk,
        //  api,
        createLogger()
      )
    )
  )
  if (module.hot) {
    // Enable Webpack hot module replacement for reducers
    module.hot.accept('./reducers', () => {
      store.replaceReducer(rootReducer)
    })
  }
  return store
}

/**
 * Production gets a simpler more performant store:
 */
const configureStoreProd = preloadedState => createStore(
  rootReducer,
  preloadedState,
  applyMiddleware(
    thunk
    // api
  )
)

export default process.env.NODE_ENV === 'production' ? configureStoreProd : configureStoreDev

/**
 * Cosmos Gets its own store
 */
export const cosmosStore = configureStoreDev
