import React from 'react'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Link from '@material-ui/core/Link'
import Toolbar from '@material-ui/core/Toolbar'
// import MenuIcon from '@material-ui/core/Menu'
import { FederatedButtons } from 'aws-amplify-react'
import { withStyles } from '@material-ui/core/styles'
import style from './auth.style'
import AuthContainer from './AuthContainer'

const SignIn = ({ classes, loading, signIn,
  handleSignup, federated,
  handleForgotPassword,
  handleInputChange,
  handleSignIn, ...props }) => {
  return (
    <AuthContainer>
      {/* {props.federated && (
        <FederatedButtons {...props} />
      )} */}
      <TextField
        id='username'
        fullWidth
        key='username'
        name='username'
        label='Username'
        autoFocus
        className={classes.textField}
        margin='normal'
        onChange={handleInputChange}
      />
      <TextField
        fullWidth
        id='password'
        label='Password'
        key='password'
        name='password'
        type='password'
        className={classes.textField}
        autoComplete='current-password'
        margin='normal'
        onChange={handleInputChange}
      />
      <Link component='button' variant='body2' className={classes.forgotPassword} disabled={loading} onClick={handleForgotPassword}>
          Forgot Password
      </Link>
      <Toolbar disableGutters>

        <Button component='button' color='secondary' variant='contained' disabled={loading} onClick={handleSignup}>
          New Account
        </Button>
        <div className={classes.spacer} />
        <Button
          className='enterAction'
          component='button'
          color='primary'
          variant='contained'
          disabled={loading}
          onClick={handleSignIn}
        >
          Sign In
        </Button>
      </Toolbar>
    </AuthContainer>
  )
}

SignIn.propTypes = {
  classes: PropTypes.object.isRequired,
  loading: PropTypes.bool,
  handleForgotPassword: PropTypes.func.isRequired,
  handleInputChange: PropTypes.func.isRequired,
  handleSignIn: PropTypes.func.isRequired
}

export default withStyles(style)(SignIn)
