let { useCallback, useState, useLayoutEffect, useRef } = require('react')

const getSize = (el, minSize) => {
  if (!el) return minSize || { width: 0, height: 0 }

  if (minSize) {
    return {
      width: Math.max(minSize.width, el.offsetWidth),
      height: Math.max(minSize.height, el.offsetHeight)
    }
  }
  else {
    return {
      width: el.offsetWidth,
      height: el.offsetHeight
    }
  }
}

const useComponentSize = (ref, minSize) => {
  const resizeActiveRef = useRef()
  const [resizeActive, setResizeActive] = useState(true)
  resizeActiveRef.current = resizeActive
  const [size, setComponentSize] = useState(
    getSize(ref ? ref.current : {}, minSize)
  )

  const handleResize = useCallback(() => {
    if (!resizeActiveRef.current) return
    if (ref.current) setComponentSize(getSize(ref.current, minSize))
  }, [ref])

  useLayoutEffect(() => {
    if (!ref.current) return

    handleResize()

    if (typeof ResizeObserver === 'function') {
      let resizeObserver = new ResizeObserver(() => handleResize())
      resizeObserver.observe(ref.current)

      return () => {
        resizeObserver.disconnect(ref.current)
        resizeObserver = null
      }
    }
    else {
      window.addEventListener('resize', handleResize)

      return () => {
        window.removeEventListener('resize', handleResize)
      }
    }
  },
  [ref.current])

  return { size, setResizeActive }
}

export default useComponentSize
