import React from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import IconButton from '@material-ui/core/IconButton'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'
import CancelIcon from '@material-ui/icons/Cancel'
import { withStyles } from '@material-ui/core/styles'
import { ListItemIcon } from '@material-ui/core'

const style = theme => ({
  root: {

  }
})

const ClusterSelected = ({ classes, seriesCount, reducedPhotoCount, onZoomTo, onClose }) => {
  console.log('cluster selected', { seriesCount, reducedPhotoCount })
  return (
    <List>
      <ListItem className={classes.root}>
        <ListItemIcon onClick={onZoomTo}>
          <ZoomOutMapIcon />
        </ListItemIcon>
        <ListItemText
          onClick={onZoomTo}
          primary={`${seriesCount} Photo series`}
          secondary={`containing ${reducedPhotoCount} individual photo${reducedPhotoCount === 1 ? '' : 's'}`}
        />
        <ListItemSecondaryAction>
          <IconButton aria-label='Close' onClick={onClose}>
            <CancelIcon />
          </IconButton>
        </ListItemSecondaryAction>

      </ListItem>
    </List>
  )
}

ClusterSelected.propTypes = {
  classes: PropTypes.object.isRequired,
  seriesCount: PropTypes.number.isRequired,
  reducedPhotoCount: PropTypes.number.isRequired,
  onZoomTo: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}

export default withStyles(style)(ClusterSelected)
