import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import CssBaseline from '@material-ui/core/CssBaseline'
import BottomNavigation from '@material-ui/core/BottomNavigation'
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction'

import { withStyles } from '@material-ui/core/styles'
import style from './Home.style'
import TopBar from '../components/TopBar'

import { AppContext } from '../contexts'
import { home } from '../config'

import PlaceIcon from '@material-ui/icons/Place'
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted'
import PersonIcon from '@material-ui/icons/Person'

const Home = ({ classes, currentState, setHomeState, children }) => {
  const { signOut } = useContext(AppContext)

  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <div className={classes.topBar} >
        <TopBar signOut={signOut} />
      </div>
      <div className={classes.mapContainer}>
        {children}
      </div>
      <BottomNavigation
        value={currentState}
        onChange={(e, newState) => setHomeState(newState)}
        showLabels
        className={classes.bottomNav}
      >
        <BottomNavigationAction value={home.AppStateTypes.MAP} label='Map' icon={<PlaceIcon />} />
        <BottomNavigationAction value={home.AppStateTypes.LIST} label='List' icon={<FormatListBulletedIcon />} />
        <BottomNavigationAction value={home.AppStateTypes.USER} label='Profile' icon={<PersonIcon />} />
      </BottomNavigation>
    </div>
  )
}

Home.propTypes = {
  classes: PropTypes.object.isRequired,
  children: PropTypes.any.isRequired,
  // From Redux
  setHomeState: PropTypes.func,
  currentState: PropTypes.string
}

export default withStyles(style)(Home)
