import React from 'react'
import PropTypes from 'prop-types'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import DialogActions from '@material-ui/core/DialogActions'

// import MenuIcon from '@material-ui/icons/MenuIcon'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({

})

const CancelConfirm = ({ classes, title, message, onConfirm, open }) => (
  <Dialog
    className={classes.root}
    disableBackdropClick
    fullWidth
    open={open}
    disableEscapeKeyDown
    maxWidth='xl'
    aria-labelledby='confirmation-dialog-title'
  >
    <DialogTitle id='confirmation-dialog-title'>{title}</DialogTitle>
    <DialogContent>
      <Typography>
        {message}
      </Typography>

    </DialogContent>
    <DialogActions>
      <Button onClick={() => onConfirm(false)} color='primary'>
      No
      </Button>
      <Button onClick={() => onConfirm(true)} color='secondary'>
      Yes
      </Button>
    </DialogActions>
  </Dialog>
)

CancelConfirm.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool,
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  onConfirm: PropTypes.func.isRequired
}

export default withStyles(style)(CancelConfirm)
