import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'

import CssBaseline from '@material-ui/core/CssBaseline'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import ArrowBackIcon from '@material-ui/icons/ArrowBack'

import AddCameraIcon from '@material-ui/icons/AddAPhoto'
import MoreIcon from '@material-ui/icons/MoreVert'

import { withStyles } from '@material-ui/core/styles'

import MapDot from '../../maps/MapDot'
import AddFab from './controls/AddFab'

import ContextMenu from './controls/ContextMenu'

import SeriesInfo from './subViews/SeriesInfo'
import SwipeShow from './subViews/SwipeShow'
import PhotoGrid from './subViews/PhotoGrid'

import DeleteDialog from './dialogs/DeleteDialog'

import { SeriesContext } from '../contexts'

import style from './SeriesView.style'

const SeriesView = ({
  classes, onSlideshowStart, onChange, allowEdit,
  onEditSeries, onDeletePhotos, onCameraAdd,
  onDeleteSeries, onLibraryAdd, onTileClick, onMenuBack
}) => {
  const [contextEl, setContextEl] = useState()
  const [deleteConfirm, setDeleteConfirm] = useState(false)

  const { series, seriesConfig } = useContext(SeriesContext)

  const onDeleteConfirm = (confirmation) => {
    setDeleteConfirm(false)
    if (confirmation === true) onDeleteSeries(series.id)
  }

  const viewport = { zoom: 16 }
  if (series.longitude && series.latitude) {
    viewport.longitude = series.longitude
    viewport.latitude = series.latitude
  }

  const [listTypeTab, setListTypeTab] = useState(0)
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position='static' className={classes.appBar}>
        <Toolbar>
          <IconButton className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant='subtitle1' color='inherit' className={classes.grow}>
           Series
          </Typography>
          { allowEdit && (
            <IconButton
              aria-haspopup='true'
              color='inherit'
              onClick={event => setContextEl(event.currentTarget)}
            >
              <MoreIcon />
            </IconButton>
          )}

          <ContextMenu anchorEl={contextEl} onClose={() => setContextEl(null)} menuItems={[
            { text: 'Edit Series', action: onEditSeries },
            { text: 'Delete Series', action: () => setDeleteConfirm(true) }
          ]} />
        </Toolbar>
        <Tabs variant='fullWidth'
          value={listTypeTab}
          onChange={(e, value) => setListTypeTab(value)}
        >
          <Tab label='Info' />
          <Tab label='Map' />
          <Tab label='Photos' />
        </Tabs>
      </AppBar>

      <div className={classes.inner}>
        {listTypeTab === 0 && (
          <React.Fragment>
            {series.photos && series.photos.length > 0 && (
              <SwipeShow
                photos={series.photos}
                onSlideshowStart={onSlideshowStart}
                onImageSelect={onTileClick}
              />
            )}
            <SeriesInfo series={series} seriesConfig={seriesConfig} onChange={onChange} />
          </React.Fragment>
        )}
        {series.photos && listTypeTab === 2 && (
          <PhotoGrid
            photos={series.photos}
            allowEdit={allowEdit}
            onTileClick={onTileClick}
            onDelete={onDeletePhotos}
          />
        )}
      </div>

      {listTypeTab === 1 && (
        <MapDot
          viewport={viewport}
          direction={series.direction}
        />
      )}
      {listTypeTab === 0 && allowEdit && (
        <AddFab
          className={classes.AddFab}
          onCameraClick={onCameraAdd}
          onLibraryClick={onLibraryAdd}
        >
          <AddCameraIcon />
        </AddFab>
      )}

      <DeleteDialog
        open={deleteConfirm}
        onConfirm={onDeleteConfirm}
        title='Delete Series'
        message='Are you sure you want to delete this series? The record and all images associated with it will be gone forever.'
      />
    </div>
  )
}

SeriesView.propTypes = {
  classes: PropTypes.object.isRequired,
  allowEdit: PropTypes.bool,
  // Action functions
  onSlideshowStart: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onDeletePhotos: PropTypes.func.isRequired,
  onDeleteSeries: PropTypes.func.isRequired,
  onCameraAdd: PropTypes.func.isRequired,
  onEditSeries: PropTypes.func.isRequired,
  onLibraryAdd: PropTypes.func.isRequired,
  onTileClick: PropTypes.func.isRequired,
  onMenuBack: PropTypes.func.isRequired
}

export default withStyles(style)(SeriesView)
