import React from 'react'
import PropTypes from 'prop-types'
import Fab from '@material-ui/core/Fab'
import MyLocationIcon from '@material-ui/icons/MyLocation'

import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    border: '1px solid',
    background: theme.palette.grey[100],
    color: 'black',
    width: 40,
    height: 40,
    zIndex: 10,
    position: 'absolute'
    // margin: theme.spacing.unit / 2,
    // padding: theme.spacing.unit / 2,
    // border: '2px solid',
    // borderRadius: theme.spacing.unit,
    // background: theme.palette.common.white,
    // fontSize: 20,
    // position: 'absolute',
    // zIndex: 100,
    // bottom: theme.spacing.unit * 10,
    // right: theme.spacing.unit * 2
  }
})

const MyLocationButton = ({ classes, theme, onClick, controlPadding, disabled }) => {
  return (
    <Fab
      disabled={disabled}
      style={{
        top: 56 + (controlPadding.top ? controlPadding.top : theme.spacing.unit * 2),
        right: controlPadding.right ? controlPadding.right : theme.spacing.unit * 2
      }}
      aria-label='myLocation'
      className={classes.root}
      onClick={onClick}
    >
      <MyLocationIcon />
    </Fab>
  )
}

MyLocationButton.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
  controlPadding: PropTypes.object,
  onClick: PropTypes.func.isRequired,
  disabled: PropTypes.bool
}

export default withStyles(style, { withTheme: true })(MyLocationButton)
