import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Toolbar from '@material-ui/core/Toolbar'
import InputBase from '@material-ui/core/InputBase'
import Drawer from '@material-ui/core/Drawer'
import InputAdornment from '@material-ui/core/InputAdornment'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import MenuIcon from '@material-ui/icons/Menu'
import SearchIcon from '@material-ui/icons/Search'
import AppLogo from '../icons/AppLogo'

import InboxIcon from '@material-ui/icons/MoveToInbox'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import MailIcon from '@material-ui/icons/Mail'

// import MenuIcon from '@material-ui/core/Menu'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: '100%'
  },
  searchIcon: {
    opacity: 0.3,
    marginRight: theme.spacing.unit
  },
  input: {
    background: '#FFFFFF',
    border: '1px solid #AAAAAA',
    borderRadius: theme.spacing.unit * 2,
    paddingLeft: theme.spacing.unit,
    marginLeft: 0,
    flex: 1
  },
  iconButton: {
    padding: 10
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4
  }
})

const TopBar = ({ classes, signOut }) => {
  const [drawerOpen, setDrawerOpen] = useState(false)
  return (
    <Toolbar className={classes.root} disableGutters>
      <IconButton className={classes.iconButton}>
        <AppLogo />
      </IconButton>
      <InputBase
        className={classes.input}
        placeholder='Search...'
        endAdornment={
          <InputAdornment position='end'>
            <SearchIcon className={classes.searchIcon} />
          </InputAdornment>
        }
      />

      <Divider className={classes.divider} />
      <IconButton className={classes.iconButton} aria-label='Menu' onClick={() => setDrawerOpen(!drawerOpen)}>
        <MenuIcon />
      </IconButton>
      <Drawer anchor='top' open={drawerOpen} onClose={() => setDrawerOpen(false)}>
        <div
          tabIndex={0}
          role='button'
          onClick={() => setDrawerOpen(false)}
          onKeyDown={() => setDrawerOpen(false)}
        >
          <div className={classes.fullList}>
            <List>
              {['Menu Item', 'Menu Item', 'Menu Item', 'Menu Item'].map((text, index) => (
                <ListItem button key={`index-${index}`}>
                  <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </List>
            {/* <Divider /> */}
            <List>
              <ListItem button onClick={signOut}>
                <ListItemIcon>
                  <ExitToAppIcon />
                </ListItemIcon>
                <ListItemText primary='Sign Out' />
              </ListItem>
            </List>
          </div>

        </div>
      </Drawer>
    </Toolbar>
  )
}

TopBar.propTypes = {
  classes: PropTypes.object.isRequired,
  signOut: PropTypes.func.isRequired
}

export default withStyles(style)(TopBar)
