
import React, { useState } from 'react'
import PropTypes from 'prop-types'

import TextField from '@material-ui/core/TextField'
import FormLabel from '@material-ui/core/FormLabel'

import { withStyles } from '@material-ui/core/styles'
import MapDot from '../../../maps/MapDot'

import MapDirPicker from '../dialogs/MapDirPicker'
import BigTextEdit from '../dialogs/BigTextEdit'

import style from './SeriesEditInner.style'
import ShortLongText from '../controls/ShortLongText'

const DIALOGS = {
  MAPPICK: 'mapPicker',
  NOTEEDIT: 'noteEdit'
}

/**
 * SeriesEdit appears in two places so we need this to be a bit reusable
 */
const SeriesEdit = ({ classes, series, delta, onChange }) => {
  const [activeDialog, setActiveDialog] = useState(null)

  const viewport = { zoom: 18 }
  if ((delta.latitude || series.latitude) && (delta.longitude || series.longitude)) {
    viewport.latitude = delta.latitude || series.latitude
    viewport.longitude = delta.longitude || series.longitude
  }

  return (
    <div>
      <TextField
        label='Series Name'
        fullWidth
        placeholder='New Series'
        className={classes.field}
        value={delta.name || series.name || ''}
        onChange={e => onChange({ ...delta, name: e.target.value })}
      />
      <div className={classes.mapWrapper} onClick={() => setActiveDialog(DIALOGS.MAPPICK)}>
        <FormLabel className={classes.mapFormLabel}>Location</FormLabel>
        <div className={classes.mapInnerWrap}>
          <MapDot
            staticMap
            className={classes.map}
            direction={delta.direction || series.direction}
            viewport={viewport}
          />
        </div>
      </div>
      <ShortLongText
        onClick={() => setActiveDialog(DIALOGS.NOTEEDIT)}
        className={classes.field}
        label='Series Note'
        text={delta.note || series.note}
      />
      {activeDialog === DIALOGS.MAPPICK && (
        <MapDirPicker
          open
          title='Series Location & Direction'
          latitude={delta.latitude || series.latitude}
          longitude={delta.longitude || series.longitude}
          direction={delta.direction || series.direction}
          onOk={(data) => {
            onChange({ ...delta, ...data })
            setActiveDialog(null)
          }}
          onCancel={() => setActiveDialog(null)}
        />
      )}
      {activeDialog === DIALOGS.NOTEEDIT && (
        <BigTextEdit
          open
          title='Series Note'
          body={delta.note || series.note}
          multiline
          onOk={(newNote) => {
            onChange({ ...delta, note: newNote })
            setActiveDialog(null)
          }}
          onCancel={() => setActiveDialog(null)}
        />
      )}
    </div>
  )
}

SeriesEdit.propTypes = {
  classes: PropTypes.object.isRequired,
  // now some functions
  series: PropTypes.object.isRequired,
  delta: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired
}

export default withStyles(style)(SeriesEdit)
